import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MainStoreComponent } from './main-store/main-store.component';
import { HomeStoreComponent } from './home-store/home-store.component';

const storeRoutes: Routes = [
  {
    path: '', component: MainStoreComponent, children: [
      { path: '', redirectTo: 'home', pathMatch: 'full' },
      { path: 'home', component: HomeStoreComponent },
      { path: 'cart', loadChildren: './cart/cart.module#CartModule' }
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(storeRoutes)],
  exports: [RouterModule]
})
export class StoreRoutingModule { }



