import { NgModule } from '@angular/core';

import { NglModule } from 'ng-lightning/ng-lightning';
import { CommonModule } from '@angular/common';
import { FlexLayoutModule } from '@angular/flex-layout';
import { CardModule } from '../../common/components/card';
import { StoreRoutingModule } from './store.routing';
import { StoreService } from './store.service';
import { MainStoreComponent } from './main-store/main-store.component';
import { HomeStoreComponent } from './home-store/home-store.component';
import { VoucherCardModule } from '../../common/components/voucher';

@NgModule({
  imports: [
    CommonModule,
    StoreRoutingModule,
    NglModule,
    FlexLayoutModule,
    CardModule,
    VoucherCardModule
  ],
  declarations: [
    MainStoreComponent,
    HomeStoreComponent
  ],
  providers: [StoreService]
})
export class StoreModule { }



