import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { TradeService } from '../../../common/trade/trade.service';
import { UserService } from '../../../common/user';
import "rxjs/add/operator/takeWhile";
import { Observable } from 'rxjs/Observable';
import { HttpClient } from '@angular/common/http';
import { VoucherService } from 'app/common/voucher/voucher.service';
import { StoreService } from 'app/modules/store/store.service';

declare var jquery: any;
declare var $: any;
declare var BoxesFx: any;

@Component({
  selector: 'app-main-store',
  templateUrl: './main-store.component.html',
  styleUrls: ['./main-store.component.css']
})
export class MainStoreComponent implements OnInit {

  eventwork: number;
  categories: any[];
  open: boolean;
  selected: any;
  bannerStore = 'store-banner.jpg';
  private pick;

  constructor(private userService: UserService, private router: Router, private voucherService: VoucherService, private storeService: StoreService) {
    this.eventwork = 1;
  }

  ngOnInit() {
    if (window.screen.width <= 959) {
      this.bannerStore = 'store-banner-xs.jpg';
    }
    this.getCategories();
    // new BoxesFx(document.getElementById('boxgallery'));
  }

  gotoDispensarie(dispensarie: any) {
    this.router.navigate(['/dispensaries/view-dispensaries', dispensarie]);
  }
  gotoProduct(code: any) {
    this.router.navigate(["/voucher/details-voucher/", code]);
  }
  changeCategory(category: any) {
    this.selected = category;
    this.storeService.setCategory(category);
  }
  getCategories() {
    this.voucherService.getCategories().subscribe((r) => {
      this.categories = r;
    })
  }

  lookupAsync = (query: string): Observable<any[]> => {
    if (!query) {
      return null;
    }
    return this.userService.search(query).map(response => response.vouchers);
  }
  changed() {
    this.gotoProduct(this.pick.id);
  }

  onChangeEvent(event: number, selected: string) {

    var $container = $('.albumContainer');
    $container.isotope({
      filter: '*',
      animationOptions: {
        duration: 750,
        easing: 'linear',
        queue: false
      }
    });
    $('.albumFilter li').on('click', function () {
      var selector = $(this).attr('data-filter');
      $container.isotope({
        filter: selector,
        animationOptions: {
          duration: 750,
          easing: 'linear',
          queue: false
        }
      });
      return false;
    });
    this.eventwork = event;
  }

}



