import { Component, OnInit, OnDestroy } from '@angular/core';
import { VoucherService } from '../../../common/voucher/voucher.service';
import { StoreService } from 'app/modules/store/store.service';

@Component({
  selector: 'home-store',
  templateUrl: 'home-store.component.html',
  styleUrls: ['./home-store.component.css']
})
export class HomeStoreComponent implements OnInit, OnDestroy {

  private vouchers: any = [];
  private alive: boolean = true;
  currentPage = 0;
  categoryCode = null;
  totalProducts = 0;

  constructor(private voucherService: VoucherService, private storeService: StoreService) {
  }

  ngOnInit() {
    this.getVouchers();
    this.storeService.categoryChanged.subscribe((c) => {
      console.log("Category changed",c)
      this.categoryCode = c.categoryCode;
      this.getVouchers();
    })
  }

  ngOnDestroy() {
    this.alive = false;
  }

  getVouchers() {
    this.vouchers = []
    this.voucherService.getVouchers(this.categoryCode, '4', 0, 10, 1).subscribe(
      res => {
        for (var i in res) {
          this.voucherService.getVoucherDetail(res[i].voucherVendorLocationCode).takeWhile(() => this.alive).subscribe(
            res => {
              this.vouchers.push(res);
            });
        }
      });
  }
}



